* {
  color: #fff;
  font-family: Helvetica Neue, Helvetica, Arial, sans-serif;
}

body {
  margin: 0;
  padding: 0;
  font-size: 16px;
  overflow: hidden;
}

header {
  height: 50px;
  width: 100%;
  text-align: center;
  background-color: #1d1d1e;
  margin: auto;
  font-size: 1.2rem;
  font-weight: 600;
  position: fixed;
}

header div {
  line-height: 50px;
  display: block;
}

h1 {
  margin: 1rem;
  font-size: 4rem;
}

h2 {
  margin: .5rem;
  font-size: 1.5rem;
}

.page {
  text-align: center;
  justify-content: center;
  align-items: center;
  display: flex;
}

.page:first-child {
  background-color: #0353a4;
}

.page:nth-child(2) {
  background-color: #061a40;
}

.page:nth-child(3) {
  background-color: #0353a4;
}

.anim-page.page:nth-child(3) .anim-me {
  animation: 1.5s ease-in-out backwards firstTimeAnim;
  display: block;
}

@keyframes firstTimeAnim {
  0%, 100% {
    letter-spacing: 0;
  }

  50% {
    letter-spacing: 4rem;
  }
}

.page:nth-child(4) {
  background-color: #061a40;
}

.btn {
  cursor: pointer;
  background-color: #0d85da;
  border-radius: 1rem;
  padding: 1rem 2rem;
  display: inline-block;
}

.btn:hover {
  background-color: #2699ea;
}

